<template>
  <div id="page-user-list">
    <vs-card class="p-3 pb-1">
      <div class="flex flex-wrap">
        <div class="w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2">
          <h5 class="m-0 pt-3">Church Accounts</h5>
        </div>
        <div class="w-full sm:w-1/2 md:w-1/2 lg:w-1/2 xl:w-1/2 text-right">
          <vs-button color="primary" to="/create-church-account" type="filled">Create Church Account</vs-button>
        </div>
      </div>
    </vs-card>
    <vs-card class="p-3 pb-1 vs-con-loading__container">
      <vs-table id="div-with-loading" search max-items="5" pagination :data="churches">
        <template slot="thead">
          <!-- <vs-th sort-key="email">Id</vs-th> -->
          <vs-th>Id</vs-th>
          <vs-th>Date created</vs-th>
          <vs-th>Church Name</vs-th>
          <vs-th>Pastor</vs-th>
          <vs-th>Mobile Number</vs-th>
          <vs-th>Branches</vs-th>
          <vs-th>Token</vs-th>
          <vs-th>Location</vs-th>
          <vs-th>Action</vs-th>
        </template>

        <template slot-scope="{data}">
          <vs-tr :key="index" v-for="(tr, index) in data">
            <vs-td :data="tr.id">{{ index + 1 }}</vs-td>
            <vs-td :data="tr.createdAt">{{ tr.createdAt | formatDate }}</vs-td>
            <vs-td :data="tr.name">{{ tr.name }}</vs-td>
            <vs-td>{{tr.HQID.pastor}}</vs-td>
            <vs-td>{{tr.HQID.phoneNumber}}</vs-td>
            <vs-td :data="tr.branches">{{ tr.branches.length }}</vs-td>
            <vs-td :data="tr.publicToken">
              <vx-tooltip text="Copy Token" position="bottom">
                <p
                  v-clipboard:copy="tr.publicToken"
                  v-clipboard:success="onCopyToken"
                  v-clipboard:error="onErrorToken"
                  class="tokenpointer"
                >{{ tr.publicToken | truncate(7) }}</p>
              </vx-tooltip>
            </vs-td>
            <vs-td>{{tr.HQID.state}}, {{tr.HQID.country}}</vs-td>
            <vs-td>
              <vs-button
                :to="'/view-church/'+ tr._id"
                color="primary"
                type="filled"
                size="small"
                class="mr-2 mb-2"
              >View</vs-button>
              <!-- <vs-button
                :to="'/edit-church/'+ tr._id"
                color="warning"
                type="filled"
                size="small"
                class="mr-2 mb-2"
              >Edit</vs-button>-->
              <vs-button
                :to="'/create-branch/'+ tr.publicToken"
                color="dark"
                size="small"
              >Create Branch</vs-button>
            </vs-td>
          </vs-tr>
        </template>
      </vs-table>
    </vs-card>
  </div>
</template>

<script>
import moment from "moment";
import vSelect from "vue-select";
export default {
  components: {
    vSelect
  },
  data() {
    return {
      churches: []
    };
  },

  computed: {},
  methods: {
    openLoadingInDiv() {
      this.$vs.loading({
        container: "#div-with-loading",
        scale: 0.6
      });
    },
    onCopyToken() {
      this.$vs.notify({
        text: "Token Copied",
        color: "success"
      });
    },
    onErrorToken() {
      this.$vs.notify({
        text: "Error copying token",
        color: "success"
      });
    }
  },
  mounted() {
    moment().format();
    this.openLoadingInDiv();
    this.$store
      .dispatch("getChurches")
      .then(resp => {
        this.churches = resp.data;
        // console.log(this.churches);
        this.$vs.loading.close("#div-with-loading > .con-vs-loading");
      })
      .catch(err => {
        this.$vs.notify({
          title: "Load Churches",
          text: "Unable to Load list of Churches!",
          color: "danger"
        });
        this.$vs.loading.close("#div-with-loading > .con-vs-loading");
      });
  },
  created() {}
};
</script>

<style lang="scss">
#page-user-list {
  .user-list-filters {
    .vs__actions {
      position: absolute;
      right: 0;
      top: 50%;
      transform: translateY(-58%);
    }
  }
}
</style>
